.overlay{position:fixed;top:0;left:0;right:0;bottom:0;background:rgba(34,59,69,.6);z-index:9999;overflow-y:auto}@media only screen and (max-width: 767px){.overlay{overflow-y:auto}}.ReactModal__Body--open{overflow:hidden}.modal{position:absolute;top:50%;left:50%;right:auto;bottom:auto;min-width:385px;box-shadow:0 5px 40px 0 rgba(50,50,65,.25);background:#fff;outline:none;transform:translate(-50%, -50%)}@media only screen and (max-width: 767px){.modal{display:flex;align-items:center;margin:0;width:100%;border-radius:0;padding:16px;overflow-y:scroll;min-width:auto}}.modal--xs{min-width:600px}.modal--sm{min-width:800px}.modal__content{width:100%;margin:auto;position:relative}.modal__header{text-align:center;padding:13px 0;background:#223b45;font-size:20px;color:#fff;font-weight:700;line-height:24px}.modal__body{padding:15px 30px}@media(max-width: 768px){.modal__body{padding:16px}}.modal__close{display:flex;justify-content:center;align-items:center;position:absolute;top:16px;right:16px;color:#fff;font-size:16px;cursor:pointer;outline:none}.modal__close:before{content:" ✖";color:#fff;font-size:20px}